class Phone extends Component {
    render() {
        return <div style={{backgroundColor: "#ffffff"}}>
            <Header/>
            <Page/>
            <Footer/>
        </div>
    }
}
class PC extends Component {
    render() {
        return <div style={{backgroundColor: "#ffffff"}}>
            <Header/>
            <Page/>
            <Footer/>
        </div>
    }
}

export class Invitation extends Component {
    constructor(props) {
        super(props);
    }

    changeLanguage = () => {
        if (localStorage['language'] == 'en') {
            localStorage['language'] = 'zh-CN';
        }
        else {
            localStorage['language'] = 'en';
        }
        location.href = location.href;
    };

    render() {
        return <div>
            {
                IsPcOrNot.get() ?
                    <PC/> : <Phone/>
            }
        </div>
    }
}

export default Invitation


